<template>
  <div class="container">
    <div class="boxes-container">
      <div ref="myBox" v-for="(item, index) in prizeList" :key="index"
        :class="['item', index === activeItem ? 'active-item' : '']"
      >
        <span class="item-text">{{item}}</span>
      </div>
    </div>
    <div class="bottom-btn">今日抽奖剩余次数：{{rollChance}}</div>
    <popup-prize v-if="isPopupShow"></popup-prize>
  </div>
</template>
<script type="text/javascript">
  import PopupPrize from './popupPrize.vue'
  export default {
    name: '',
    components: {
      PopupPrize
    },
    data () {
      return {
        prizeList: [
          '二等奖',
          '一等奖',
          '三等奖',
          '四等奖',
          '',
          '五等奖',
          '六等奖',
          '六等奖',
          '六等奖',
        ],
        movePath: [0, 1 ,2, 5, 8, 7, 6, 3],  //9宫格的转动路径
        activeIndex: 0, // 当前激活的项目下标
        activeItem: null, // 当前激活的项目
        rollBtnDisabled: false,
        rollChance: 2,
        isPopupShow: false,
        isUserFollowed: true
      }
    },
    props: [''],
    computed: {
    },
    methods: {
      startRoll () {
        if (this.rollBtnDisabled || this.rollChance <= 0) return
        this.rollBtnDisabled = true
        this.rollChance--
        let randomNum = parseInt(Math.random() * 8) // 获取0到7之间的随机数
        let intervalTimer = setInterval(this.moveNext, 100)
        setTimeout(() => { 
          clearInterval(intervalTimer)
          this.rollBtnDisabled = false
          this.showPopup()
         }, randomNum * 100 + 2000)
      },
      moveNext () {
        if (this.activeIndex === 8) this.activeIndex = 0
        this.activeItem = this.movePath[this.activeIndex]
        this.activeIndex++
      },
      showPopup () {
        setTimeout(() => {
          if (this.isUserFollowed) {
            this.$router.push('/red_envelope')
          } else {
            this.isPopupShow = true
          }
        },800)
      }
  
    },
    mounted () {
      let boxList = this.$refs.myBox
      boxList[4].onclick = this.startRoll
    }
  }
</script>
<style>
html, body, #app {
  height: 100%;
}
.container {
  width: 100%;
  height: 100%;
  background-image: url('../assets/images/bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.boxes-container {
  width: 320px;  
  height: 320px;
  display: grid;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 100px 100px 100px;
  grid-gap: 10px;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 20%;
}
.boxes-container div:nth-child(5) {
  background-image: url('../assets/images/box-center.png');
  background-size: contain;
  background-repeat: no-repeat;
}
.boxes-container div:not(:nth-child(5)) {
  background-image: url('../assets/images/box.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.item {
  display: block;
  width: 100%;
  height: 100%;

  position: relative;
}
.item-text {
  position: absolute;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  top: 25%;
  color: #fff;
  font-size: 12px;  
  font-weight: 600;
}
.active-item {
  background-image: url('../assets/images/box-active.png') !important;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.bottom-btn {
  background-color: #ff8401;
  width: 50%;
  color: #fff;
  font-size: 18px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 13%;
  margin: auto;
  box-shadow: 0px 2px 6px #000;
}
</style>


