<template>
  <div class="popup-container" >
    <div class="prize-container">
      <div class="red-bag-box">
        <div class="red-bag">
          <img  src="@/assets/images/red-bag.png" alt="">
        </div>
        <div class="text">
          <img  src="@/assets/images/prize-text-top.png" alt="">
        </div>
      </div>
      <div class="qrcode-box">
        <div>
          <img class="qrcode" src="@/assets/images/qrcode.jpg" alt="">
        </div>
        <div>
          <img src="@/assets/images/prize-text-bottom.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    name: '',
    components: {},
    data () {
      return {}
    },
    props: ['']
  }
</script>
<style>
.popup-container {
  background-image: url('../assets/images/popup-container.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 90%;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 20%;
  padding: 20px;
}
.prize-container .red-bag-box {
  margin-top: 10px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.red-bag-box .red-bag {
  width: 30%;
  padding-right: 10px;
}
.red-bag-box .text {
  width: 50%;
}
.red-bag-box img {
  width: 100%;
}
.qrcode-box {
  text-align: center;
}
.qrcode-box img {
  width: 80%;
}
</style>


